.topheaderMain{

    width: 97vw;
    height: 95vh;
    border: 5px solid #000;
    margin: auto;
} 
.brandLogo{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}
.brandText{
    font-size: 1.9rem;
    text-decoration: none;
}
.brnadLogoIcon{
    width: 3rem;
    height: 3rem;
    border-radius: 2rem;
    border: 1px solid #000;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2.2rem;
    background-color: tomato;
}
.topMenu{
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    padding: 0.5rem;
    border: 1px solid red;
    background-color: #ddd;
    margin-top: 1rem;
    border-radius: 2rem;
}
.topMenuList{
    display: flex;
    align-items: stretch;
    justify-content: center;
}
.topMenuList ul{
    list-style-type: none;
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 1.2rem;
}
.topMenuContact{
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: #ddd; */
    /* padding: 0.5rem; */
    border-radius: 2rem;
    
}
.topMenuContact a{
    background-color: yellow;
    padding: 0.8rem 1rem;
    border-radius: 2rem;
    font-size: 1.2rem;
    text-decoration: none;
}

.topheaderbanner{
    display: flex;
    border: 1px solid red;
    padding: 1rem;
}
.thbl_row1{
    margin: 2rem 0px 1rem 0px;
}
.thbl_row1_inside{
    border: 1px solid #000;
    padding: 0.5rem 1rem;
    position: relative;
    display: inline-block;
}
.thbl_row1_inside span.thbl_corner_top:before,
.thbl_row1_inside span.thbl_corner_top:after,
.thbl_row1_inside span.thbl_corner_bottom:before,
.thbl_row1_inside span.thbl_corner_bottom:after{
    position: relative;
    content: "";
    /* padding: 1rem; */
    border: 1px solid #000;
    height: 1rem;
    width: 1rem;

}

.thbl_row1_inside span.thbl_corner_top:before{
    position: absolute;
    top: -10%;
    left: -5.2%;
    width: 0.6rem;
    height: 0.5rem;
    border: 1px solid green;
    background-color: green;
}
.thbl_row1_inside span.thbl_corner_top:after{
    position: absolute;
    top: -10%;
    right: -5.2%;
    width: 0.6rem;
    height: 0.5rem;
    border: 1px solid green;
    background-color: green;
}
.thbl_row1_inside span.thbl_corner_bottom:before{
    position: absolute;
    bottom: -10%;
    right: -5.2%;
    width: 0.6rem;
    height: 0.5rem;
    border: 1px solid green;
    background-color: green;
}
.thbl_row1_inside span.thbl_corner_bottom:after{
    position: absolute;
    bottom: -10%;
    left: -5.2%;
    width: 0.6rem;
    height: 0.5rem;
    border: 1px solid green;
    background-color: green;
}
.thbl_row1_inside p{
    font-size: 1.5rem;
}
.thbl_row2_inside .myName{
    color: tomato;
    font-weight: bold;
    /* text-decoration: underline; */
    position: relative;
    font-style: italic;
}
.thbl_row2_inside .myName:after{
    position: absolute;
    content: "";
    width: 90%;
    top: 105%;
    left: 0;
    border: 3px solid red;
}
.thbl_row2_inside p{
    /* line-height: 1.5rem; */
    font-size: 4rem;
    margin: 0.3rem 0rem;
    letter-spacing: 2px;
}
.thbl_row3{
    margin: 2rem 0px;
}
.thbl_row3_inside p{
    width: 70%;
    font-size: 1.2rem;
    letter-spacing: 0.5px;
    /* border: 1px solid red; */
}
.thbl_row4{
    margin: 2rem 0px;
}
.thbl_row4_button_wrap{
    background-color: yellow;
    display: flex;
    gap: 1rem;
    /* width: 30%; */
    padding: 0.2rem 0.2rem;
    border-radius: 2rem;
}
.thbl_row4_button_wrap .viewMe{

}
.thbl_row4_button_wrap .playButton{
    display: flex;
    align-items: center;
    justify-content: center;
}
.thbl_row4_button_wrap .viewMe a{
    background-color: blue;
    color: #fff;
    padding: 1rem;
    display: block;
    border-radius: 2rem;
    text-decoration: none;
    font-size: 1.3rem;
}
.thbl_row4_button_wrap .playButton a{
    background-color: #fff;
    /* color: #fff; */
    /* padding: 1rem; */
    display: block;
    border-radius: 2rem;

}
.thbl_row4_button_wrap .playButton a img{
    width: 2.5rem;
    height: 2.5rem;
    object-fit: contain;
    display: block;
}

.thbl_row4_hireMe{
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #000;
    padding: 0px 2rem;
    border-radius: 2rem;
    font-size: 1.3rem;
}
.thbl_row4_inside{
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;

}
.topheaderbannerleft{
    flex-basis: 50%;
}
.topheaderbannerright{
    flex-basis: 50%;
    overflow: hidden;
}
.topheaderbannerright_inside{
    /* width: 100%; */
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
}
.topheaderbannerright_inside img{
    /* width: 100%; */
    height:100%
}
.topheaderbannerright{
    background-image: url("../icons/blob.svg");
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
}
.thbr_website_text, 
.thbr_marketing_text{
    position: absolute;
    background-color: #ddd;
    padding: 0.7rem 1.5rem;
    font-size: 1.5rem;
    position: absolute;
    border-radius: 2rem;
    color: #fff;
    font-weight: bold;
    /* border: 1px solid #000; */
}

.thbr_website_text{
     
    bottom: 10%;
    right: 10%;
     
}
.thbr_marketing_text{
    
    top: 50%;
    left: 10%;
     

}
.services_list{
    overflow: hidden;
}
.services_list_inside{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    width: 130vw;
    height: 4rem;
}
.services_list_inside p{
    font-size: 3rem;
    
}
.services_list_inside div.servicesseparator{
    width: 1rem;
    height: 1rem;
}
.services_list_inside div.servicesseparator img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.services_list_inside div:not(.servicesseparator){
    /* white-space: nowrap; */
    flex-grow: 1;
    text-align: center;

}
.services_list_inside{
    animation: textScroll 15s linear infinite alternate;
}

@keyframes textScroll{
    0% { transform: translateX(-30%); }
    50% { transform: translateX(0%); }
    100% { transform: translateX(-22%); }
}